/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState, useRef } from 'react'

import './styles/base.sass'

import Project from './components/Projects'
import Contact from './components/Contack'
import Skills from './components/Skills'
import Menu from './components/Menu'
import Home from './components/Home'
import Work from './components/Work'

const App = () => {
  const [scroll, setScroll] = useState(0)
  const project = useRef()
  const contact = useRef()
  const skills = useRef()
  const home = useRef()
  const work = useRef()

  const html = document.getElementsByTagName('html')[0]

  
  useEffect(()=>{
    const event = () => setScroll(html.scrollTop)
    
    window.addEventListener('scroll', event)
    
    return () => window.removeEventListener('scroll',event)
  },[])

 
  return (
    <div className="app-container">
      <Menu 
        scroll={scroll}
        home={home} 
        skills={skills}
        work={work} 
        project={project} 
        contact={contact}
      ></Menu>
      <Home forwardRef={home} next={skills}></Home>
      <Skills forwardRef={skills}></Skills>
      <Work forwardRef={work}></Work>
      <Project forwardRef={project}></Project>
      <Contact forwardRef={contact}></Contact>
    </div>
  );
}

export default App;
